@import 'components/reset/reset.scss';

// px单位转成vw单位
@function px2vw($size: 14px) {
  @if (type-of($size) == "number" and unit($size) == "px") {
  @return $size * 100vw / 1920px;
  } @else {
  @return $size;
  }
}                  /* Firefox版本19+ */
body{
  width: 100%;
  height: 100vh;
  background:#000;
}
.swiper1{
  width: 100%;
  height: 100vh;
 
  .slide1{
    width: 100%;
    height: 100vh;
    position: absolute;
    background:url(../images/bg1.jpg) no-repeat top center #000;
    top: 0;
    left: 0;
    .d1{
      position: absolute;
      width: px2vw(1020px);
      left: px2vw(236px);
      top: px2vw(250px);
      padding-bottom: px2vw(40px);
      background: url(../images/line.png) bottom left no-repeat;
      background-size: px2vw(1020px);
    }
    .img1{
      width: px2vw(548px);
    }
    .d2{
      position: absolute;
      left: px2vw(220px);
      top: px2vw(600px);

    }
    .yuan{width: px2vw(76px); animation: shake 5s linear infinite;}
    .yuan2{width: px2vw(76px);position: absolute;top: 0;left: 0;}
    .img2{
      width: px2vw(283px);
      position: absolute;
      top: 25%;left: px2vw(35px);
      transform: translateY(-50%);
    }
    .d3{
      position: absolute;
      right: 0;
      top: px2vw(150px);
    }
    .yx{
      width: px2vw(971px);
    }
  }
  
}
.slide2{
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  background:url(../images/bg1.jpg) no-repeat top center #000;
  display: none;
  .img3{
    position: absolute;
    top: 0;
    width: px2vw(1120px);
    left: 50%;
    transform: translateX(-50%);

  }
  .img4{
    position: absolute;
    width: px2vw(106px);
    top: px2vw(100px);
    left: px2vw(80px);
  }
  .img5{
    position: absolute;
    width: px2vw(304px);
    left: px2vw(50px);
    top: px2vw(550px);
    display: none;
  }
  .nav{
    position: absolute;
    right: px2vw(80px);
    top: 50%;
    transform: translateY(-50%);
    li{
      padding: px2vw(10px) px2vw(16px);
      height: px2vw(230px);
      margin-bottom: 2px;
      border: 1px solid #fff;
      display: flex;
      align-items: center;
      box-sizing: content-box;
    }
    li.active{border-right: px2vw(6px) solid #ff0f0f;}
    img{
      width: px2vw(28px)
    }
  }
}

@keyframes shake{
  0%{
    transform: rotate(0);
  }
  100%{
    transform: rotate(360deg);
  }
}


@import "animation.scss";
@import "mobile.scss";
