/*声明 WebFont*/
body{padding:0;margin:0;font:normal 12px/180% 'Helvetica,Arial,Verdana'; color:#333;background:#dd5653;}
h1,h2,h3,h4,h5,h6,hr,p,blockquote,dl,dt,dd,ul,ol,li,pre,form,button,input,textarea,th,td{margin:0;padding:0;}
div{margin:0 auto;text-align:left;font:normal 12px/180% 'Helvetica,Arial,Verdana';}
a:link,a:visited{color:#000;text-decoration:none}
a:hover{color:#3b84cc;text-decoration:none}
img{ border:none}
ol,ul,li{list-style:none;}
i{font-style:normal;}
input,img{vertical-align: middle;}
input[type="button"],input[type="submit"]{vertical-align: middle;cursor: pointer;}
// html {font-size: 62.5%;10 ÷ 16 × 100% = 62.5%}

/*html5*/
article,aside,dialog,footer,header,section,footer,nav,figure,menu{display:block; margin:0 auto;}
/*clear*/
.clearfix:before,.clearfix:after {content:"";display:table;}
.clearfix:after{clear:both;overflow:hidden;}
.clearfix{zoom:1;}
.clear{ clear:both;}