.flash{
    -webkit-animation:flash 2s forwards;
    animation:flash 2s forwards;
  }
  @-webkit-keyframes flash{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
  }
  @keyframes flash{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
.flyAnimation{
    opacity: 1;
    -webkit-animation:fly 33s ease infinite;
    animation:fly 33s ease infinite;
}
.flyAnimation1{
    opacity: 1;
    -webkit-animation:fly1 35s ease infinite;
    animation:fly1 35s ease infinite;
}
@-webkit-keyframes fly{
    0%{
        -webkit-transform: translateX(0) translateY(0);
        transform: translateX(0) translateY(0);
    }
    25%{
        -webkit-transform: translateX(40px) translateY(60px);
        transform: translateX(40px) translateY(60px);
    }
    50%{
        -webkit-transform: translateX(-30px) translateY(30);
        transform: translateX(-30px) translateY(30px);
    }
    75%{
        -webkit-transform: translateX(-30px) translateY(0);
        transform: translateX(-30px) translateY(0);
    }
    100%{
        -webkit-transform: translateX(0) translateY(0);
        transform: translateX(0) translateY(0);
    }
  }
@keyframes fly{
    0%{
        transform: translateX(0) translateY(0);
    }
    25%{

        transform: translateX(40px) translateY(60px);
    }
    50%{
        transform: translateX(-30px) translateY(30px);
    }
    75%{
        transform: translateX(-30px) translateY(0);
    }
    100%{
        transform: translateX(0) translateY(0);
    }
}
@-webkit-keyframes fly1{
    0%{
        -webkit-transform: translateX(0) translateY(0);
        transform: translateX(0) translateY(0);
    }
    25%{
        -webkit-transform: translateX(-20px) translateY(-30px);
        transform: translateX(-20px) translateY(-30px);
    }
    50%{
        -webkit-transform: translateX(-10px) translateY(10px);
        transform: translateX(-130px) translateY(10px);
    }
    75%{
        -webkit-transform: translateX(-20px) translateY(0);
        transform: translateX(-20px) translateY(0);
    }
    100%{
        -webkit-transform: translateX(0) translateY(0);
        transform: translateX(0) translateY(0);
    }
  }
@keyframes fly1{
    0%{
        transform: translateX(0) translateY(0);
    }
    25%{

        transform: translateX(-20px) translateY(-30px);
    }
    50%{
        transform: translateX(-10px) translateY(10px);
    }
    75%{
        transform: translateX(-20px) translateY(0);
    }
    100%{
        transform: translateX(0) translateY(0);
    }
}
.flash1{
    -webkit-animation:flash1 2s forwards;
    animation:flash1 2s forwards;
  }
  @-webkit-keyframes flash1{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
  }
  @keyframes flash1{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
/*提示运动*/
.p1_jiao{
    -webkit-animation:p1_jiao 3s ease infinite;
    animation:p1_jiao 3s ease infinite;
}
@-webkit-keyframes p1_jiao{
    0%{
        -webkit-transform:translateY(0);
        transform:translateY(0);
        opacity:0;
    }
    100%{
        -webkit-transform: translateY(40px);
        transform: translateY(40px);
        opacity: 1;
    }
  }
@keyframes p1_jiao{
    0%{
        transform: translateY(0px);
        opacity: 0;
    }
    100%{
        transform: translateY(40px);
        opacity: 1;
    }
}
/*音乐转*/
.audioOpen{
    -webkit-animation:audioOpen 305s infinite;
    animation:audioOpen 305s infinite;
    transform-origin:center center;
}
@-webkit-keyframes audioOpen{
    0%{
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100%{
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
  }
@keyframes audioOpen{
    0%{
        transform: rotate(0);
    }
    100%{
        transform: rotate(360deg);
    }
}
/*按钮旋转*/
.ButtonAnimation{
    -webkit-animation:zhuan 2s infinite;
    animation:zhuan 2s infinite;
    transform-origin:center center;
}
@-webkit-keyframes zhuan{
    0%{
        -webkit-transform: scale(1);
        opacity: 0.3;
    }
    100%{
        -webkit-transform: scale(1.1);
        opacity:1;
    }
  }
@keyframes zhuan{
    0%{
        transform: scale(1);
        opacity: 0.3;
    }
    100%{
        transform: scale(1.1);
        opacity:1;
    }
}