@media (min-device-width:320px) and (max-width:689px),
       (max-device-width:480px)
	   {
      body{
        background-image:none;
      }
      .desc{
        width:90%;
        margin:0 auto;
        margin-bottom:20px;
        color:#fff;
        font-size:12px;
        text-indent:2em;
        font-family:微软雅黑
      }
      .banner {
        display: block;
        width: 100%;
      }
      .banner img{
        width: 100%;
      }
      .top{
        display: none;
      }
       
      .content{
        width: 100%;
        margin:0 auto;
        height:auto;
        padding: 0;
        h3{
          text-align: center;
          font-size: 16px;
          padding-bottom:15px;
          color: #fff;
          border-bottom:1px solid #f2f2f2;
          width: 95%;
          margin: 0 auto;
         
        }
        .great{
          overflow: hidden;
          width: 100%;
          margin: 0 auto;
          display: flex;
          flex-direction: row;
          align-items: center;
          flex-wrap: wrap;
          justify-content: space-between;
          width: 90%;
          li{
            float: left;
            margin-right:0;
            margin-top: 10px;
            width: 48%;
            img{
              width: 100%;
            }
          }
          li:nth-child(5n){
            margin-right: 0;
          }
        }
        .main{
          .block{
            display: none;
            position: relative;
            overflow: hidden;
            width: 100%;
          }
          .block:nth-child(1){
            display: block;
          }
          .siwper-container{
            width: 90%;
            margin-top: 10px;
            overflow: hidden;
            .swiper-slide{
              text-align: center;
              margin-top: 10px;
              img{
                width: 100%;
              }
            }
          }
          .swiper-button-prev{
            left: 0;
          }
          .swiper-button-next{
            right: 0;
          }
          .siwper-container:nth-child(1){
            display: block;
          }
        }
      
        .nav{
          border-bottom:1px solid #f2f2f2;
          overflow: hidden;
          cursor: pointer;
          width: 95%;
          margin: 0 auto;
          margin-top: 30px;
          li{
            float: left;
            font-size: 12px;
            padding-bottom:15px;
            color: #fff;
            width: 16.5%;
            text-align: center;
          }
          li.active{
            color: #92d4f3;
          }
          li:hover{
            color: #92d4f3;
          }
        }
      }

      .side{
        display: none;
      }
      .copyright{
        text-align: center;
        color: #fff;
        font-size: 10px;
        padding:30px 0;
      }
       }